import { header } from '../elements';

if (header.self !== null) {
    const hamburger = header.hamburger;
    const nav = header.navContainer;
    const close = header.close;

    console.log(hamburger);

    let xPos = 0;
    const navShow = () => {
        nav.style.transform = `translateX(${-xPos}%)`;
        xPos += 10;

        if (xPos <= 100) {
            requestAnimationFrame(navShow);
        }

        if (xPos === 90) {
            nav.style.backgroundColor = `rgba(0,0,0,.4)`;
        }
    };

    const navHide = () => {
        nav.style.transform = `translateX(${-xPos}%)`;
        xPos -= 10;

        if (xPos >= 0) {
            requestAnimationFrame(navHide);
        }

        if (xPos === 90) {
            nav.style.backgroundColor = `rgba(0,0,0,0)`;
        }
    };

    hamburger.addEventListener('click', navShow);
    close.addEventListener('click', navHide);
}
